<template>
<van-pull-refresh v-model="isLoading" @refresh="onRefresh">
  <div class="main">
    <!--  -->
    <div class="header_box">
      <h4>{{NewsDetails.title}}</h4>
      <div class="NewsDetails_header">
        <span class="f_float">{{NewsDetails.add_time | dateformat('yyyy-MM-DD')}}</span>
        <span class="r_float">{{NewsDetails.click}}</span>
      </div>
    </div>
    <!--  -->
    <div v-html="NewsDetails.content" class="main_body"></div>
    <!--  -->
    <comment></comment>
  </div>
</van-pull-refresh>
</template>
<script>
import Comments from '../Comments.vue'
export default {
  components: {
    comment: Comments
  },
  props: [],
  data () {
    return {
      //  
      isLoading: false,
      NewsDetails: {}
    }
  },
  created () {
    this.getNewsDetails()
  },
  mounted () {},
  methods: {
    // id 
    async getNewsDetails () {
      //   console.log(this.getid)
      // 
      const { data: Details } = await this.$http.get(
        '/api/getnew/' + this.getid
      )
      // console.log(Details)
      if (Details.status === 0) {
        // this.$toast.success('')
        this.NewsDetails = Details.message[0]
        // console.log(this.NewsDetails)
      } else {
        this.$toast.fail('')
      }
    },
    // 
    onRefresh () {
      setTimeout(() => {
        this.$toast('')
        this.isLoading = false
        this.count++
      }, 1000)
    }

  },
  computed: {
    // url id
    getid () {
      return this.$route.params.id
    }
  }
}
</script>
<style lang="less" scoped>
// scopedstyle   
// scoped 

.header_box {
  color: #1989fa;
  //   padding: 5px;
}
.f_float {
  float: left;
}
.r_float {
  float: right;
}
h4 {
  margin-top: 10px;
  text-align: center;
  font-size: 14px;
  font-weight: 700;
}
.NewsDetails_header {
  height: 20px;
  font-size: 12px;
  border-bottom: 1px solid #ccc;
}
.main {
  padding: 5px;
}
.comment {
  text-align: left;
  border-bottom: 1px solid #ccc;
}
.main_body p {
  color: #535353;
}
</style>
