<template>
  <div>
    <section class="main" v-if="lists.length === 0">
      <!--  -->
      <div class="box">
        <!--  -->
        <div class="shopcar">
          <img src="../../../public/images/car.png" alt="">
        </div>
        <p></p>
        <router-link class="gotoShopping" to="/goods/list">
          <van-button type="info"></van-button>
        </router-link>
      </div>
    </section>

    <section v-else>
      <!--  -->
      <!--   -->
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
        <div>
          <div v-for="item in lists" :key="item.id">
            <van-swipe-cell>
              <van-card
                :num="item.cou"
                :price="item.sell_price"
                :title="item.title"
                :thumb="item.thumb_path"
              >
                <template #footer>
                  <van-button color="#f2f3f5" size="mini" @click="sub(item.id)">-</van-button>
                  <van-button color="#f2f3f5" size="mini">{{item.cou}}</van-button>
                  <van-button color="#f2f3f5" size="mini" @click="add(item.id)">+</van-button>
                </template>
              </van-card>
              <template #right>
                <van-button
                  content
                  text=""
                  type="danger"
                  class="delete-button"
                  @click="openDialog(item.id)"
                />
              </template>
            </van-swipe-cell>
          </div>
        </div>
      </van-pull-refresh>
    </section>
    <!--  -->
    <van-submit-bar :price="getTotal*100" decimal-length button-text="" @submit="onSubmit" />
  </div>
</template>

<script>
import { Toast } from 'vant'
import { mapState, mapMutations, mapActions, mapGetters } from 'vuex'
export default {
  props: [],
  data () {
    return {
      isLoading: false
    }
  },
  computed: {
    // 
    ...mapState(['lists']),
    // ,
    ...mapGetters(['getCarLists', 'getTotal'])
  },
  create () {

  },
  mounted () {
    this.$nextTick(() => {
      this.changeToString()
      this.getLists()
    }
    )
  },
  methods: {
    // 
    goBack () {
      window.history.go(-1)
    },
    // 
    ...mapMutations([
      'initlists', 'add', 'sub'
    ]),

    // 
    onRefresh () {
      setTimeout(() => {
        this.getLists()
        location.reload()
        Toast('')
        this.isLoading = false
      }, 1000)
    },
    // 
    openDialog (id) {
      this.$dialog.confirm({
        message: ''
      }).then(() => {
        // instance.close();
        this.deleteRun(id)
      }).catch(() => {
        // console.log(err)
      })
    },
    // 
    // 
    onSubmit () {
      // 
      this.$router.push('/pay')
    },
    // ,
    ...mapActions(['getLists', 'deleteRun', 'changeToString'])
  }
}
</script>
<style lang="less" scoped>
/deep/.van-nav-bar__text{
  color: #fff
}
/deep/.van-icon-arrow-left::before{
  color: #fff
}
.van-nav-bar {
  background-color: #1989fa;
  font-size: 18px;

}
/deep/.van-nav-bar__title{
  color: #fff

}
.main{
  width: 100%;
  height: 270px;
  // background-color: #f00;
  display:flex;
  flex-direction: column;
  justify-content:space-around;
  align-items: center;

}
.box{
  display:flex;
  flex-direction:column;
  align-items:center;
}
.shopcar{
  display:flex;
  justify-content:center;
  width: 100px;
  height: 100px;
  background-color: #e2a5a3;
  border-radius: 50%;
  img{width:100%;
  flex:1}
}
p{ color:#dbdbdb;
font-size:24px
}
/deep/.van-card__content {
  justify-content:start
}
/deep/.van-card__price:first-child{
  color: red;
}
/deep/.van-card__footer>button{
  color: #000!important
}
.van-nav-bar{
  position: fixed;
  top:0;
  width: 100%;
}

/deep/.van-swipe-cell__right>button{
  height: 100%;
}
/deep/ .van-submit-bar{
  position: fixed;
  bottom:50px;

}
</style>
