<template>
  <div>
    <!--  -->
    <van-nav-bar
      title=".vant"
      left-text=""
      right-text=""
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
      :fixed="true"
    />
    <!--  -->
    <van-search
      show-action
      placeholder=""
      @search="onSearch"
      @cancel="onCancel"
      clearable
      v-model="searchText"
    />
    <!--  -->

    <van-swipe-cell v-for="(item, index) in getListshop" :key="index">
      <van-cell :border="false" :title="item.name">{{item.ctime|dateformat()}}</van-cell>
      <template #right>
        <van-button square type="danger" text="" @click="deleteshop(item.id)" />
      </template>
    </van-swipe-cell>

    <!--  -->
    <div v-show="!showHistory">
      <van-cell title="">
        <van-icon name="delete" size="18" @click="clearHistory" />
      </van-cell>
      <van-tag
        type="primary"
        size="large"
        v-for="(item,index) in searchHistory"
        :key="index"
        @click="handleHistory($event)"
      >{{item}}</van-tag>
      <van-divider v-show="!searchHistory.length"></van-divider>
    </div>

    <!--  -->
    <!-- <van-popup v-model="show" position="top" :style="{ height: '3%' }"></van-popup>
    <van-popup v-model="show" position="top" :style="{ height: '3%' }"></van-popup>-->
  </div>
</template>

<script>
import { Dialog } from 'vant'
export default {
  props: [],
  data () {
    return {
      getListshop: [],
      searchText: '',
      showHistory: false,
      searchHistory: []
    }
  },
  mounted () {
    this.getList()
    let res = localStorage.getItem('history')
    if (res === null) {
      res = []
    } else if (res === '') {
      res = []
    } else {
      res = res.split(',')
    }
    this.searchHistory = res
  },
  methods: {
    // 
    async getList () {
      if (!this.searchText.trim()) return
      const { data: res } = await this.$http.get(
        'http://api.cms.liulongbin.top/api/getprodlist'
      )
      this.getListshop = res.message
      this.showHistory = true
    },
    onClickLeft () {
      this.$router.push('/home')
    },
    // 
    onClickRight () {
      this.$router.push('/addgoods')
    },
    // 
    async onSearch () {
      this.showHistory = true
      if (this.searchHistory === '') {
        this.searchHistory = []
        this.searchHistory.push(this.searchText.trim())
      } else {
        const index = this.searchHistory.indexOf(this.searchText.trim())
        if (index === -1) {
          this.searchHistory.push(this.searchText.trim())
        }
      }
      localStorage.setItem('history', this.searchHistory)
      this.getList()
    },
    // 
    onCancel () {
      this.searchText = ''
      this.getListshop = []
      this.showHistory = false
    },
    // 
    async deleteshop (id) {
      Dialog.confirm({
        title: '',
        message: '',
        confirmButtonColor: 'red'
      })
        .then(async () => {
          const { data: res } = await this.$http.get('/api/delproduct/' + id)
          this.$notify({ type: 'success', message: res.message })
          this.getList()
          this.searchText = ''
        })
        .catch(() => {
          this.$notify({ type: 'primary', message: '' })
        })
    },
    // 
    handleHistory (e) {
      this.searchText = e.target.innerText
    },
    // 
    clearHistory () {
      localStorage.setItem('history', [])
      this.searchHistory = []
    }
  }

}
</script>

<style lang="less" scoped>

.van-search {
  padding: 8px 8px;
  height: 60px;
}
.van-search .van-cell {
  padding: 5px 5px 5px 0;
}
/deep/.van-field__control {
  color: #323233;
}
/deep/.van-cell span {
  color: #323233;
  font-size: 18px;
}
[data-v-ded676a2] .van-cell span {
  font-size: 14px;
  color: #323233;
}
.van-tag {
  margin: 11px;
  background-color: #969799;
}
</style>
